.secondPage {
  display: flex;
  padding-left: 32px;
  padding-right: 32px;
  margin-bottom: 72px;
}
.secondBg {
  display: flex;
  background: url(../Img/secondBg.svg) no-repeat;
  background-size: cover;
  width: 100%;
  height: 669px;
  align-items: center;
  justify-content: flex-end;
}
.secondInfoBlock {
  width: 480px;
  height: 509px;
  background: rgba(255, 255, 255, 1);
  display: flex;
  flex-direction: column;
  padding-top: 52px;
  padding-left: 32px;
  padding-right: 32px;
}
.secondInfoBlock p {
  font-family: "Source Sans Pro";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 32px;
  color: #1d1a17;
  width: 374px;
  height: 64px;
  margin-bottom: 56px;
}
.secondInfo {
  width: 415px;
  height: 256px;
  font-family: "Crimson Text";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 32px;
  color: #1d1a17;
  margin-bottom: 21px;
}
.secondArray {
  font-family: "Crimson Text";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #1d1a17;
  background: url(../Img/array.svg) no-repeat right center;
  width: 132px;
  text-decoration: none;
}
